.main_menu .mob_menu {
    display: none;
    width: 30px;
    height: 30px;
    padding: 13px 0px 0px;
    cursor: pointer;
    position: relative;
}

.main_menu .mob_menu span {
    display: block;
    width: 30px;
    height: 5px;
    border-radius: 2px;
    background: #FE5A03;
    position: relative;
    transition: all 0.3s linear;
}

.main_menu .mob_menu span::before {
    content: '';
    display: block;
    width: 30px;
    height: 5px;
    border-radius: 2px;
    background: #FE5A03;
    position: absolute;
    top: -13px;
    left: 0px;
    transition: all 0.3s linear;
}

.main_menu .mob_menu span::after {
    content: '';
    display: block;
    width: 30px;
    height: 5px;
    border-radius: 2px;
    background: #FE5A03;
    position: absolute;
    top: 13px;
    left: 0px;
    transition: all 0.3s linear;
}

.main_menu.open .mob_menu span {
    background: transparent;
    transform: rotate(90deg);
}

.main_menu.open .mob_menu span::before {
    transform: rotate(45deg);
    top: 0px;
}

.main_menu.open .mob_menu span::after {
    transform: rotate(-45deg);
    top: 0px;
}

.main_menu .menu {
    display: flex;
    margin: 0px;
