.sect_full_house .myclock {
    width: 350px;
    height: 398px;
    position: relative;
    background: url("../img/sect_full_house_bg.png") no-repeat center center;
    margin: 0 auto;
    top: 80px;
}

.clock__hand {
    font-size: inherit;
    position: absolute;
    display: block;
    left: 50%;
    top: 50%;
}

.clock__hand--hour::after {
    content: "";
    background: url("../img/strelka-m.png") no-repeat 100% center;
    font-size: inherit;
    position: absolute;
    display: block;
    height: 102px;
    width: 7px;
    bottom: 50%;
    left: 50%;
    margin: 0 0 0 -4px;
}

.clock__hand--hour::before {
    content: "";
    background: url("../img/strelka-m2.png") no-repeat 100% center;
    font-size: inherit;
    position: absolute;
    display: block;
    height: 24px;
    width: 7px;
    bottom: 50%;
    left: 50%;
    margin: 0 0 -24px -4px;
}

.clock__hand--minute::after {
    content: "";
    background: url("../img/strelka-m.png") no-repeat 100% center;
    font-size: inherit;
    position: absolute;
    display: block;
    height: 129px;
    width: 5px;
    bottom: 50%;
    left: 50%;
    margin: 0 0 0px -2px;
}

.clock__hand--minute::before {
    content: "";
    background: url("../img/strelka-m2.png") no-repeat 100% center;
    font-size: inherit;
    position: absolute;
    display: block;
    height: 32px;
    width: 5px;
    bottom: 50%;
    left: 50%;
    margin: 0 0 -32px -2px;
}

.clock__hand--second::after {
    content: "";
    background: url("../img/strelka-s.png") no-repeat 100% center;
    font-size: inherit;
    position: absolute;
    display: block;
    height: 156px;
    width: 19px;
    bottom: 50%;
    left: 50%;
    margin: 0 0 -10px -10px;
}

.clock__hand--second::before {
    content: "";
    background: url("../img/strelka-s2.png") no-repeat center 100%;
    font-size: inherit;
    position: absolute;
    display: block;
    height: 22px;
    width: 19px;
    bottom: 50%;
    left: 50%;
    margin: 0 0 -32px -10px;
}

.clock__hand--hour {
    animation: clock-hand-rotate 12200s linear infinite;
}

.clock__hand--minute {
    animation: clock-hand-rotate 600s linear infinite;
}

.clock__hand--second {
    animation: clock-hand-rotate 10s linear infinite;
}

@keyframes clock-hand-rotate {
    from {
        transform: rotate(0deg)
    }
    to {
        transform: rotate(360deg)
    }
}

