.img_wrap {
    width: 100%;
    position: relative;
}

.slider-for {
    display: block;
    position: relative;
    width: 100%;
}

.slider-for .slick-next,
.slider-for .slick-prev {
    position: absolute;
    top: 49%;
    display: block;
    width: 44px;
    height: 44px;
    z-index: 111;
    text-indent: -9999px;
    margin: -27px 0 0 0;
    border: none;
    border-radius: 50%;
    background-color: #FFF;
    opacity: 0.5;
    transition: 0.5s;
    background-repeat: no-repeat;
}

.slider-for .item {
    width: 100%;
    height: 700px;
    overflow: hidden;
}

.slider-for .item::after {
    content: '';
    display: block;
    width: 100%;
    height: 100%;
    background: url("../img/zoom.png") no-repeat center;
    background-size: 30px;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    opacity: 0;
    transition: opacity 0.3s ease;
}

.slider-for .item img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.slider-for .slick-prev {
    background-image: url(../img/slider_arrow_act_prev1.png);
    background-position: center center;
    background-size: 8px auto;
    left: 26px;
}

.slider-for .slick-next {
    background-image: url(../img/slider_arrow_act_next1.png);
    background-position: center center;
    background-size: 8px auto;
    right: 18px;
}

.slider-for:hover .slick-next,
.slider-for:hover .slick-prev {
    opacity: 1;
}

.slider-for .slick-next:hover {
    background-image: url(../img/slider_arrow_act_next.png);
}

.slider-for .slick-prev:hover {
    background-image: url(../img/slider_arrow_act_prev.png);
}

.slider-nav {
    display: block;
    width: 912px;
    position: relative;
    left: -8px;
    margin: 40px 0 0 0;
}

.slider-nav .item {
    margin: 0 8px;
    position: relative;
    width: 136px;
}

.slider-nav .item:hover {
    cursor: pointer;
}

.slider-nav .item img {
    border: none;
    vertical-align: bottom;
    width: 100%;
    height: auto;
}

.slider-nav .slick-current::before {
    content: '';
    position: absolute;
    top: 0px;
    bottom: 0px;
    left: 0px;
    right: 0px;
    border: 2px solid #a3c222;
}

.slider-nav .slick-next,
.slider-nav .slick-prev {
    display: none !important;
}

@media screen and (min-width: 768px) and (max-width: 1339px) {

    .img_wrap {
        width: 100%;
